.colors {
  font-size: 0;
  span {
    display: inline-block;
    height: 0.25rem;
    width: 16.6666667%;
    &:nth-child(1) { background: $color-pink }
    &:nth-child(2) { background: $color-red }
    &:nth-child(3) { background: $color-orange }
    &:nth-child(4) { background: $color-yellow }
    &:nth-child(5) { background: $color-cyan }
    &:nth-child(6) { background: $color-green }
  }
}

.topbar {
  background: $color-almost-black;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, .25);
  color: $color-almost-white;
  height: 6rem;

  @include element('logotype') {
    @include span-columns(2);
    h1 {
      font-size: 1.25rem;
      line-height: 6rem;
    }
  }

  @include element('navigation') {
    @include span-columns(10);
    nav {
      float: right;
      line-height: 6rem;
      a {
        margin-left: $horizontal-spacing;
        color: $color-white;
        &:hover {
          color: $color-primary;
        }
      }
    }
  }
}

.subnav {
  background: $color-white;
  box-shadow: 1px 2px 2px rgba(0, 0, 0,.025);
  color: $color-almost-black;
  height: 3rem;
}